.App {
  text-align: center;
}

.App-Intro {
  font-family: 'Henny Penny', sans-serif;
  color: rgb(22, 44, 18);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
  background-color: rgb(180, 231, 221);
}

.App-logo{
  height:80px;
  margin-right:auto;
  margin-left: auto;
  display:block;
}

.App-title{
  font-size: 2em;
  font-family: 'Henny Penny', sans-serif;
}

.App-header{
  background-color: rgb(22, 44, 18);
  height: 150px;
  padding: 20px;
  color: white;
  font-family: 'Henny Penny', sans-serif;
  text-align: center;
}